:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", "sans-serif";
}

body {
  font-family: var(--font-inter);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.8125em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding-inline: 1.5625rem;
  margin: 0 auto 2.875rem;
  max-width: 69.375rem;
}
@media (min-width: 71.8125em) {
  .container {
    padding: 0;
  }
}

.cards {
  display: grid;
  gap: 1.875rem;
}
@media (min-width: 39.9375em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.8125em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card, .card-small {
  position: relative;
  overflow: hidden;
  border-radius: 0.3125rem;
  padding: 1.5625rem;
  text-align: center;
  background-color: var(--dark-card);
  color: var(--dark-text1);
  transition: all 300ms;
}
.card:hover, .card-small:hover {
  cursor: pointer;
  transform: scale(1.05);
  background-color: #374062;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--instagram {
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.3125rem;
  top: 0;
  left: 0;
  background: linear-gradient(135deg, var(--instagram-start) 0%, var(--instagram-middle) 50.91%, var(--instagram-end) 100%);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.5rem;
  margin-bottom: 1.75rem;
}
.card__username, .card-small__label {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__folowers {
  margin-bottom: 1.5625rem;
}
.card__count {
  margin-bottom: 0.3125rem;
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  color: var(--light-bg);
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small, .card-small__count {
  font-size: 1.875rem;
  margin-bottom: 0;
}
.card__label {
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
  color: var(--dark-text1);
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.bottom-cards {
  display: grid;
  gap: 1.875rem;
}
@media (min-width: 39.9375em) {
  .bottom-cards {
    grid-template-columns: 1fr;
  }
}
@media (min-width: 71.8125em) {
  .bottom-cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.subbottom-cards {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  gap: 1.875rem;
}
@media (min-width: 39.9375em) {
  .subbottom-cards {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
  }
}
@media (min-width: 71.8125em) {
  .subbottom-cards {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
  }
}

.card-small {
  padding: 1.875rem;
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
.card-small__upper, .card-small__lower {
  display: flex;
  justify-content: space-between;
}
.card-small__lower {
  align-items: end;
}
.header {
  display: flex;
  margin-top: 2.25rem;
  flex-wrap: wrap;
}
@media (min-width: 39.9375em) {
  .header {
    justify-content: space-between;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 39.9375em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 400;
  color: var(--dark-text1);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 0.3rem 0.5rem;
  border: none;
}
.toggle label {
  font-size: 0.875rem;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  grid-row: 1/2;
  justify-self: end;
  align-self: center;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=light] {
  grid-column: 3/4;
  grid-row: 1/2;
  align-self: center;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  width: 1.25rem;
  height: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid white;
}
.toggle__background {
  pointer-events: none;
  display: block;
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 300ms ease;
}
#light:checked ~ .toggle__button {
  right: 3px;
  left: auto;
}
#system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}